<template>
  <layout>
    <div slot="top">
      <banner :types="types" />
    </div>
    <div slot="content">
      <router-view />
    </div>
  </layout>
</template>

<script>
import Banner from '@/components/basic/banner'
import Layout from '@/components/layout/layout'
export default {
  components: {
    Banner,
    Layout
  },
  data () {
    return {
      // 是否是视频 1否 2是
      types: 1
    }
  },
  mounted () {
    const { path } = this.$route
    // 路径为详细资料时
    if (path === '/ds/detail') {
      console.log('mounted')
      this.types = 2
    }
  }
}
</script>

<style lang="less">
</style>



